body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{height:100vh;width:100vw}.root *::-webkit-scrollbar{width:0px}


.index{display:flex;flex-direction:column;height:100vh;width:100vw}.index .animation{overflow:hidden;position:absolute;width:100vw;height:100vh}.index .animation .upLoad{margin:0px;top:-100vh;position:absolute;background-color:#f1f1f1;height:50vh;width:100vw;z-index:100;-webkit-animation:topMove 6s;animation:topMove 6s}.index .animation .downLoad{margin:0px;top:200vh;position:absolute;background-color:#f1f1f1;height:50vh;width:100vw;z-index:100;-webkit-animation:bottomMove 6s;animation:bottomMove 6s}.index iframe{transform:scale(1.3);position:fixed;z-index:1}.index .body{display:flex;justify-content:center;height:100%;z-index:2;font-family:"Roboto", sans-serif}.index .body .box{align-self:center;border-radius:5px;color:white;border:1px solid rgba(255,255,255,0.9);background-color:rgba(0,0,0,0.3);display:flex;flex-direction:column;width:80%;height:80%}.index .body .box header{display:flex;width:100%}.index .body .box header .airplaneIcon{margin:1.5rem 0 0 2.5rem;height:3rem;width:3rem;-webkit-filter:invert(100%);filter:invert(100%);opacity:0.8}.index .body .box header .pages{display:flex;font-size:1.1rem;font-weight:300;justify-content:space-between;width:40%;height:3rem;margin:2rem 0 2rem 2rem}.index .body .box header .pages .pagesItem{z-index:1;position:relative;overflow:hidden;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.index .body .box header .pages .pagesItem a{position:relative;text-decoration:none;color:white;transition:0.2s all;z-index:3;padding:0rem 0.5rem}.index .body .box header .pages .pagesItem a:hover{color:black}.index .body .box header .pages .pagesItem::before{content:"";position:absolute;width:100%;height:100%;background-color:rgba(255,255,255,0.9);z-index:2;top:100%;transition:0.2s all}.index .body .box header .pages .pagesItem:hover::before{top:0}.index .body .box header .search{margin-left:auto}.index .body .box header .search .searchContainer{display:flex;justify-content:center;align-items:center;border-radius:0 0 0 5px;background-color:rgba(255,255,255,0.9);width:100%;height:5rem}.index .body .box header .search .searchContainer input{margin:0 10px;outline:none;border:none;background-color:rgba(255,255,255,0);color:black;font-size:1.3rem;mix-blend-mode:multiply}.index .body .box header .search .searchContainer img{margin:0 10px;height:1.5rem;-webkit-filter:invert(30%) saturate(200%);filter:invert(30%) saturate(200%)}.index .body .box section.container{display:flex}.index .body .box section.container .title{display:flex;flex-direction:column;height:100%;margin-left:8rem;margin-top:3rem}.index .body .box section.container .title h1{font-weight:bold;font-size:4rem;margin:0px}.index .body .box section.container .title a{font-family:"Roboto", sans-serif;font-weight:100;font-size:2.7rem}.index .body .box section.container .title button{cursor:pointer;width:138px;height:2rem;outline:none;border:none;background-color:#f5862c;font-family:"Roboto", sans-serif;font-weight:bolder;color:white;box-shadow:0 0 2px 1px rgba(0,0,0,0.3);transition:0.2s all}.index .body .box section.container .title button:hover{box-shadow:0 0 2px 1px rgba(0,0,0,0.5)}.index .body .box footer{font-family:"Roboto", sans-serif;font-weight:100;font-size:0.8rem;display:flex;margin-top:auto;width:100%;justify-content:space-between}.index .body .box footer .creditContainer{z-index:1;position:relative;overflow:hidden;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding:10px 0}.index .body .box footer .creditContainer a{position:relative;text-decoration:none;color:white;transition:0.2s color;z-index:3;padding:10px 5px}.index .body .box footer .creditContainer a:hover{color:black;font-weight:300}.index .body .box footer .creditContainer::before{content:"";position:absolute;width:100%;height:100%;background-color:rgba(255,255,255,0.9);z-index:2;top:100%;transition:0.2s all}.index .body .box footer .creditContainer:hover::before{top:0}.index .body .box footer .myCredits{padding:8px;color:white;border-radius:0 5px 0 0}.index .body .box footer .credits{padding:8px;color:white;border-radius:5px 0 0 0}@-webkit-keyframes topMove{0%{top:0}50%{top:0}100%{top:-100vh}}@keyframes topMove{0%{top:0}50%{top:0}100%{top:-100vh}}@-webkit-keyframes bottomMove{0%{top:50vh}50%{top:50vh}100%{display:none;top:150vh}}@keyframes bottomMove{0%{top:50vh}50%{top:50vh}100%{display:none;top:150vh}}

